// global
@use "../../variables.scss" as *;
@use "../fonts.scss";
@use "../base-styles.scss";
@use "../title.scss";
@use "../button.scss";

.social {
  width: 320px;
  padding: 0 30px;
  outline: 2px solid #dda0dd;
}

.social__list {
  padding: 0;
  margin: 0 auto;
  width: 142px;
  min-height: 27px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  list-style-type: none;
}
